    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
    min-height: 52px;
    padding-left: 19px;
  }
}
.example-info {
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.example-info__data {
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  box-sizing: border-box;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
  text-transform: uppercase;
}
.example-info__data--weight {
  padding: 13px 41px 17px 42px;
}
.example-info__data--days {
  padding: 14px 21px 16px;
}
.example-info__text {
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  font-family: "Arial", "Helvetica", sans-serif;
  position: absolute;
  width: 57px;
  text-align: center;
  background-color: #f2f2f2;
  color: #444444;
}
.example-info__text--weight {
  padding: 4px 14px 0 20px;
  top: 43px;
  left: 13px;
}
.example-info__text--days {
  padding: 3px 14px 0 18px;
  top: 44px;
  left: 17px;
}
@media (min-width: 768px) {
  .example-info__data {
    font-size: 30px;
    line-height: 37px;
    font-weight: 400;
  }
  .example-info__data--weight {
    padding: 16px 56px 19px;
  }
  .example-info__data--days {
    padding: 16px 30px 20px;
  }
  .example-info__text {
    font-size: 14px;
    line-height: 14px;
    font-weight: 400;
    background-color: #ffffff;
  }
  .example-info__text--weight {
    width: 114px;
    padding: 0;
    padding-top: 6px;
    top: 58px;
    left: 24px;
  }
  .example-info__text--days {
    width: 145px;
    margin-top: 20px;
    padding: 0;
    top: 44px;
    left: 10px;
  }
}
@media (min-width: 1300px) {
  .example-info__text {
    background-color: #f2f2f2;
  }
}
.container {
  overflow: hidden;
}
.toggles-info {
  display: flex;
  justify-content: space-between;
  width: 197px;
  margin: 0 auto;
  padding-bottom: 37px;
}
.toggles-info__before,
.toggles-info__after {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  padding-top: 29px;
  text-transform: uppercase;
  cursor: pointer;
}
.toggles-info__bar {
  position: relative;
  width: 84px;
  height: 14px;
  margin-top: 32px;
  box-sizing: border-box;
  border-radius: 6px;
  border: 1px solid #cdcdcd;
  background-color: #ffffff;
}
.toggles-info__toggle {
  position: absolute;
  top: 3px;
  left: 8px;
  width: 35px;
  height: 6px;
  box-sizing: border-box;
  border-radius: 3px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .toggles-info {
    width: 712px;
    padding-bottom: 69px;
    align-items: center;
  }
  .toggles-info__bar {
    width: 428px;
    height: 6px;
    margin-top: 5px;
    border-radius: 3px;
    border: none;
    background-color: #dcdcdc;
  }
  .toggles-info__toggle {
    position: relative;
    top: -14px;
    left: 200px;
    width: 34px;
    height: 34px;
    border: 1px solid #cdcdcd;
    border-radius: 50%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    background-color: #ffffff;
  }
  .toggles-info__toggle::before {
    content: "";
    position: absolute;
    top: 12px;
    left: 12px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #68b738;
  }
  .toggles-info__toggle:hover {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.25);
  }
  .toggles-info__toggle:active {
    border: 2px solid #68b738;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.3);
  }
  .toggles-info__toggle:active::before {
    top: 11px;
    left: 11px;
  }
  .toggles-info__before,
  .toggles-info__after {
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
    display: inline-block;
    padding-top: 15px;
    padding-bottom: 5px;
    border-bottom: 1px dotted rgba(17, 17, 17, 0.23);
  }
  .toggles-info__before:hover,
  .toggles-info__after:hover {
    border-bottom: 1px solid rgba(17, 17, 17, 0.4);
  }
  .toggles-info__before:active,
  .toggles-info__after:active {
    border-bottom: none;
  }
  .toggles-info__before {
    margin-left: 70px;
  }
  .toggles-info__after {
    margin-right: 72px;
  }
}
@media (min-width: 1300px) {
  .toggles-info {
    width: 722px;
  }
  .toggles-info__before,
  .toggles-info__after {
    padding-top: 36px;
  }
  .toggles-info__bar {
    position: relative;
    margin-top: 29px;
  }
  .toggles-info__before {
    margin-left: 84px;
  }
  .toggles-info__after {
    margin-right: 68px;
  }
}
